.contact {
	.row {
		margin-top: 10%;
		margin-bottom: 70px;
	}
	.heading {
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;

		color: #000000;
	}
	.line {
		margin-top: 15px;
		margin-bottom: 15px;		
		width: 60%;
		background: #000000;
		height: 1px;
	}
	.heading_short {
		margin-bottom: 25px;		
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;

		color: #000000;
	}
	.heading_lower {
		margin-bottom: 35px;		
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;

		color: #000000;
	}
	.input_header {
		margin-top: 20px;
		margin-bottom: 20px;	
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;

		color: #BFBFBF;
		background: #FFFFFF;
		border: 1px solid #000000;
		box-sizing: border-box;
		border-radius: 11px;
		padding: 10px;
	}
	.btn-danger {
		width: 120px;
		float: right;
	}
}