.about {
	img {
		border-radius: 10px;
		width: calc(100% - 40px);
		height: 40vh;
		margin: 20px;
	}
	.heading {
		text-align: center;
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;

		color: #000000;
	}
	.line {
		margin-left: 30%;
		margin-top: 15px;
		margin-bottom: 15px;		
		width: 40%;
		background: #000000;
		height: 1px;
	}
	.heading_lower {
		margin-bottom: 35px;		
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: 600;
		font-size: 14px;
		line-height: 29px;
		color: #000000;
		margin-left: 5%;
		width: 90%;
		text-align: center;
	}
	.row {
		margin-left: 40px;
		width: calc(100% - 80px);
		background: #06979D;
		border-radius: 15px;
		.col-md-6 {
			padding: 10px;
		}
		.heading_vision {
			color: white;
			font-weight: 600;
			font-size: 20px;
			text-align: center;
		}
	}
}