.login {
	background: rgba(5, 119, 123, 0.85);
	backdrop-filter: blur(20px);
	height: 100vh;
	width: 100vw;
	.container {
		.login_form {
			.row {
				margin-left: 0px !important;
				margin-right: 0px !important;
			}
			background: #DADADA;
			border-radius: 11px;
			transform: translate(70%, 25%);
			width: 35vw;
			box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.15);
			border-radius: 10px;
			padding-bottom: 10px;
			.login_heading {
				text-align: center;
				padding: 15px;
				font-weight: 600;
			}
			.non_active_login {
				color: white;
				background: #06979D;
			}
			.active_login {
				color: black;
				background:  #DADADA;
			}
			.outer_box {
				background: white;
				margin: 20px;
				padding: 10px;
				border-radius: 10px;
				.one_stop_box {
					background: white;
					border-radius: 5px;
					.one_stop {
						font-family: Roboto;
						font-style: normal;
						font-weight: normal;
						font-size: 16px;
						line-height: 16px;
						text-align: center;
						color: #807D7D;
					}
				}
				.choose_type {
					margin-top: 30px; 
					margin-bottom: 20px;
					text-align: center;
					.choose_type_active_login {
						background: #DF0000;
						color: white;
						border-radius: 5px;
					}
				}
				.input_box {
					margin: 15px;
					input {
						width: 100%;
						background: #DADADA;
						border: 1px solid #958888;
						box-sizing: border-box;
						border-radius: 10px;
						padding: 5px;
					}
				}
				.remember {
					margin-top: 20px;
					margin-left: 15px;		
					color: #807D7D;
					.forget {
						margin-right: 15px;
					}
				}
				.new_user {
					margin-top: 50px;
					color: #958888;
					text-align: center;
				}
			}
		}
	}
}
.right_border {
	border-top-right-radius: 10px;
}
.left_border {
	border-top-left-radius: 10px;
}
.left_float {
	float: right;
}
.right_margin {
	margin-right: 10px;
}