.distributor_selection {
	height: calc(100vh - 56px);
	overflow: scroll;
	.upper_heading {
		text-align: center;
		padding: 10px;
		font-size: 30px;
		color: white;
		background: #1989cd;
	}
	.container {
		.upper_heading_number {
			color: white;
			text-align: center;
			height: 40px;
			.total_distributor {
				background: #1989a1;
				padding: 10px;
				border-radius: 4px;
				float: left;
			}
			.upmapped_distributor {
				background: rgba(179, 39, 39, 0.884);
				padding: 10px;
				border-radius: 4px;
				float: right;
			}
		}
	}
}
