.internship {
	.main {
		.footer {
			margin-top: 20px;
		}
		img {
			width: 100%;
			height: 300px;
		}
		.form {
			width: 60%;
			margin-left: 5%;
			h4 {
				margin-top: 20px;
				font-size: 18px;
				line-height: 28px;
				color: #09777c;
				font-family: "Open Sans";
				font-weight: 700;
				text-align: center;
			}
			.heading {
				margin-top: 20px;
				font-size: 18px;
				line-height: 28px;
				color: #09777c;
				font-family: "Open Sans";
				font-weight: 700;
			}
			.box {
				border: 1px solid #c6c6c6;
				border-radius: 5px;
				padding: 10px;
				.col-md-6 {
					padding: 0px;
					margin: 0px;
				}
				#apply {
					background: #ed1c24;
					color: white;
					width: 200px;
					margin: 10px auto;
					display: block;
					border-radius: 5px;
				}
				label {
					margin-left: 5px;
				}
			}
		}
	}
}
