.pharmacy_profile {
	.search {
		padding-left: 15px;
		padding-right: 15px;
		padding-top: 15px;
		input {
			width: 47vw;
			border-radius: 5px;
			margin-right: 20px;
			background: #dcdada;
			color: black;
		}
	}
	.box {
		height: 36.5vh;
		overflow-y: scroll;  
		border: 1px solid grey;
		border-radius: 5px;
	}
	.box_pharma, .box_selling {
		height: 79vh;
		overflow-y: scroll;  
		border: 1px solid grey;
		border-radius: 5px;	
	}
	h5 {
		background: #1989cd;
		padding: 5px;
		color: white;
		margin-top: 10px;
		border-radius: 5px;
		margin-bottom: 0px;
		text-align: center;
	}
	table {
		font-size: 11px;
		tr:nth-child(odd) {background-color: #dad7d7;}
		th {
			background: #003366;
			color: white;
			cursor: pointer;
		}
		td {
			font-weight: 600;
		}
	}
}